import { tag0Unique, tagAllUnique, filteredSubStandards, rows, getTags, filteredStandards, getFilteredSubStandards } from '../api/spreadsheet';
import aesthetics from './aesthetics';
export let getId;
export let id;
export let idArray = [];
export let subId;
export let subIdArray = [];
export let value;
export let valueArray = [];
export let standard;
export let subtopic;
let myElement;
let title;


const generateContent = {
    tag0: (imgPath) => {
        return new Promise((resolve, reject) => {
            getId = (get) => { console.log(get) }
            for (let i = 0; i < tag0Unique.length; i++) {
                idArray.push(tag0Unique[i]);

                myElement = document.getElementById('tag0Container').innerHTML + '<div class="topicDiv"><img class="folder" src="/static/media/folder_red.90acdd63.png" alt="Topic Icon"/><p class="topic">' + tag0Unique[i] + '<p></div>'
                document.getElementById('tag0Container').innerHTML = myElement;
            }
            resolve('resolved');
        });
    },

    subtopics: () => {
        return new Promise((resolve, reject) => {
            subIdArray = []
            document.getElementById('topicList').innerHTML = '<li id="all">All</li>';
            for (let i = 0; i < tagAllUnique.length; i++) {
                subIdArray.push(tagAllUnique[i]);
                document.getElementById('topicList').innerHTML = document.getElementById('topicList').innerHTML + '<li id="' + subIdArray[i] + '" class="subtopic" value="' + i + '">' + tagAllUnique[i] + '</li>';
            }
            console.log(subIdArray);
            resolve('resolved');
        })
    },

    standards: () => {
        return new Promise((resolve, reject) => {
            document.getElementById('results').innerHTML = '';

            for (let i = 0; i < filteredStandards.length; i++) {
                title = filteredStandards[i].serial.replace(/[^\w\s]|\s/g, '').toLowerCase();
                document.getElementById('results').innerHTML = document.getElementById('results').innerHTML + '<div id=' + title + ' class="result" value=' + i +'><p class="standard-serial">' + filteredStandards[i].serial + '</p><div class="abstract-container"><p>' + filteredStandards[i].abstract + '</p></div></div>';
            }
        })
    },

    standardsBySubtopic: () => {
        document.getElementById('results').innerHTML = '';
        console.log(filteredSubStandards);
        console.log(filteredSubStandards.length);

        for (let i = 0; i < filteredSubStandards.length; i++) {
            console.log('ADDED');
            title = filteredSubStandards[i].serial.replace(/[^\w\s]|\s/g, '').toLowerCase();
            document.getElementById('results').innerHTML = document.getElementById('results').innerHTML + '<div id=' + title + ' class="result"><p class="standard-serial">' + filteredSubStandards[i].serial + '</p><div class="abstract-container"><p>' + filteredSubStandards[i].abstract + '</p></div></div>';
        }
    },

    assignTopicId: () => {
        return new Promise((resolve, reject) => {
            for (let i = 0; i < idArray.length; i++) {
                document.querySelector('.topicDiv:nth-child(' + (i + 1) + ')').id = idArray[i];
                document.querySelector('.topicDiv:nth-child(' + (i + 1) + ')').onclick = () => { subtopic = idArray[i]; getTags(rows, subtopic); generateContent.standards(); generateContent.crossRef(); generateContent.subtopics(); generateContent.assignSubtopicId(); };
            }

            resolve('resolved');
        })
    },

    assignSubtopicId: () => {
        return new Promise((resolve, reject) => {
            for (let i = 0; i < subIdArray.length; i++) {
                document.getElementById(subIdArray[i]).onclick = () => { subId = subIdArray[i]; getFilteredSubStandards(filteredStandards, subIdArray, subId); generateContent.standardsBySubtopic(); }
            }
        });
    },

    crossRef: () => {
        for (let i = 0; i < filteredStandards.length; i++) {
            document.querySelector('.result:nth-child(' + (i + 1) + ')').onclick = () => { console.log(filteredStandards[i]); };
        }
    },
}

export default generateContent;